@critical {
  * {
    box-sizing: border-box;
  }
  h1 {
    margin: 0;
  }
  button {
    vertical-align: middle;
    color: inherit;
    font: inherit;
    border: 0;
    background: transparent;
    padding: 0;
    margin: 0;
    outline: 0;
  }
  a {
    background-color: transparent;
    text-decoration: none;
    color: inherit;
  }
  html {
    font-family: 'PT Sans', sans-serif;
    color: #1d1d1d;
    font-size: 16px;
    line-height: 1.55;
  }
  html,
  body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }
}
