.videoFrame {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  border: 10px solid #1d1d1d;
  background: #1d1d1d;
}
.videoFrame:before,
.videoFrame:after {
  content: '';
  display: block;
  position: absolute;
  height: 10px;
  background: #006dfe;
}
.videoFrame:before {
  width: 44px;
  bottom: 100%;
  left: 50%;
}
.videoFrame:after {
  width: 52px;
  top: 100%;
  right: 70px;
}
.videoFrame:hover .videoFrame__play {
  background: #1d1d1d;
}
.videoFrame:hover .videoFrame__play:before {
  background: #006dfe;
}
.videoFrame--bordered {
  border-width: 20px;
}
.videoFrame--bordered:before,
.videoFrame--bordered:after {
  height: 20px;
}
.videoFrame--gray {
  border-color: #e9e9e9;
}
.videoFrame--gray:after {
  width: 29px;
  top: 100%;
  left: 147px;
}
.videoFrame--gray .videoFrame__play {
  bottom: -10px;
  right: -10px;
}
.videoFrame--gray .videoFrame__play:before {
  height: 10px;
}
.videoFrame__preview {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: 50% 50%;
}
.videoFrame__preview:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0,109,254,0) 70%, rgba(0,109,254,0.4) 100%);
}
.videoFrame__play {
  position: absolute;
  width: 193px;
  height: 94px;
  background: #006dfe;
  bottom: 0;
  right: 0;
  z-index: 1;
  transition: all 0.1s ease-in;
}
.videoFrame__play:before,
.videoFrame__play:after {
  content: '';
  display: block;
  position: absolute;
}
.videoFrame__play:before {
